import React from 'react';

import { Form, Input, Button, InputNumber } from 'antd';

const  AddDegreeForm = (props) => {

    return (
        <div>
            <Form
                name="basic"
                initialValues={{ remember: true }}
                onFinish={props.onSubmitDegree}
                layout={'vertical'}
                size={'small'}
            >
                <Form.Item
                    label="Nombre Ttulo:"
                    name="degree_name"
                    rules={[{ required: true, message: 'Por favor ingrese un nombre' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Institucion:"
                    name="institution"
                    rules={[{ required: true, message: 'Por favor ingrese un nombre' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Ao:"
                    name="year"
                    rules={[{ required: true, type: 'number', message: 'Por favor ingrese un ao valido ', min: 1900, max: 2050 }]}
                >
                    <InputNumber />
                </Form.Item>
                <Form.Item >
                    <Button
                        type="primary"
                        htmlType="submit"
                        style={{ background: "#ffbc02", color: "#231F20", borderColor: "#e3a765" }}
                    >
                        Aadir 
            </Button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default AddDegreeForm;